import styled from '@emotion/styled';
import { ModalTypes } from '../Types';

export const Body: ModalTypes['Parts']['Body'] = (props) => {
  
  return (
    <StyledSection {...props}>
      {props.children}
    </StyledSection>
  )
}

const StyledSection = styled.div<ModalTypes['Props']['Body']>`
  width            : ${({ w = '100%' }) => w};
  height           : ${({ h = '100%' }) => h};
  background-color : ${({ b = '1px solid #000' }) => b};
  padding          : ${({ p = '16px' }) => p};
`;